<template>
  <form id="quote-creation">
    <div class="accordion" id="accordionExample">
      <div class="accordion-item my-blue">
        <h2 class="accordion-header my-blue" id="headingOne">
          <button
            class="accordion-button"
            type="button"
            data-bs-toggle="collapse"
            data-bs-target="#collapseOne"
            aria-expanded="true"
            aria-controls="collapseOne"
          >
            <svg
              width="25"
              height="24"
              viewBox="0 0 25 24"
              fill="none"
              xmlns="http://www.w3.org/2000/svg"
            >
              <path
                fill-rule="evenodd"
                clip-rule="evenodd"
                d="M3.7999 0.400024C3.03077 0.400024 2.29315 0.705559 1.74929 1.24941C1.20544 1.79327 0.899902 2.5309 0.899902 3.30002V19.25C0.899902 20.4037 1.3582 21.5102 2.17399 22.3259C2.98977 23.1417 4.09621 23.6 5.2499 23.6C6.40359 23.6 7.51003 23.1417 8.32582 22.3259C9.1416 21.5102 9.5999 20.4037 9.5999 19.25V3.30002C9.5999 2.5309 9.29437 1.79327 8.75051 1.24941C8.20666 0.705559 7.46903 0.400024 6.6999 0.400024H3.7999ZM5.2499 20.7C5.63447 20.7 6.00328 20.5473 6.27521 20.2753C6.54713 20.0034 6.6999 19.6346 6.6999 19.25C6.6999 18.8655 6.54713 18.4966 6.27521 18.2247C6.00328 17.9528 5.63447 17.8 5.2499 17.8C4.86534 17.8 4.49653 17.9528 4.2246 18.2247C3.95267 18.4966 3.7999 18.8655 3.7999 19.25C3.7999 19.6346 3.95267 20.0034 4.2246 20.2753C4.49653 20.5473 4.86534 20.7 5.2499 20.7ZM12.4999 18.1524L19.6049 11.0474C20.1486 10.5035 20.454 9.76605 20.454 8.99707C20.454 8.2281 20.1486 7.4906 19.6049 6.94677L17.5532 4.89502C17.0093 4.35136 16.2718 4.04594 15.5029 4.04594C14.7339 4.04594 13.9964 4.35136 13.4526 4.89502L12.4999 5.84767V18.1524ZM21.1999 23.6H11.1529L19.8529 14.9H21.1999C21.969 14.9 22.7067 15.2056 23.2505 15.7494C23.7944 16.2933 24.0999 17.0309 24.0999 17.8V20.7C24.0999 21.4692 23.7944 22.2068 23.2505 22.7506C22.7067 23.2945 21.969 23.6 21.1999 23.6V23.6Z"
                fill="white"
              />
            </svg>
            Nastavenia cenovej ponuky
          </button>
        </h2>
        <div
          id="collapseOne"
          class="accordion-collapse collapse show"
          aria-labelledby="headingOne"
          data-bs-parent="#accordionExample"
        >
          <div class="accordion-body">
            <div class="my-container quote-settings">
              <div class="quote-side left">
                <div class="input-box">
                  <label>
                    INTERnÝ NÁZOV PONUKY
                    <input v-model="internalQuoteName" />
                  </label>
                  <img
                    :src="i"
                    class="i-icon"
                    data-bs-toggle="tooltip"
                    data-bs-placement="top"
                    title="tooltip on top"
                  />
                </div>

                <div class="input-box">
                  <label>
                    Interné ID ponuky
                    <input v-model="internalQuoteId" />
                  </label>
                  <img
                    :src="i"
                    class="i-icon"
                    data-bs-toggle="tooltip"
                    data-bs-placement="top"
                    title="tooltip on top"
                  />
                </div>

                <div class="quote-selects-flex">
                  <div>
                    <div class="form-floating mb-3">
                      <Multiselect
                        v-model="language"
                        :options="languageOptions"
                        label="label"
                        value="label"
                        selectLabel=""
                        selectedLabel=""
                        deselectLabel=""
                        deselectGroupLabel=""
                        selectGroupLabel=""
                        placeholder="Jazyk ponuky"
                        track-by="value"
                        :allowEmpty="false"
                        :show-labels="false"
                      >
                        <template slot="singleLabel" slot-scope="props">
                          <img
                            class="option__image"
                            v-if="props.option.flag"
                            :src="
                              require(`/src/assets/svg/flags/${props.option.flag}`)
                            "
                            :alt="props.option.label"
                          />
                          <span class="option__label">{{
                            props.option.label
                          }}</span>
                        </template>
                        <template slot="option" slot-scope="props">
                          <img
                            class="option__image"
                            v-if="props.option.flag"
                            :src="
                              require(`/src/assets/svg/flags/${props.option.flag}`)
                            "
                            :alt="props.option.label"
                          />
                          <span class="option__label">{{
                            props.option.label
                          }}</span>
                        </template>
                        <span slot="noResult">
                          Oops! No elements found. Consider changing the search
                          query.
                        </span>
                      </Multiselect>

                      <label for="floatingInput" style="top: -13px">
                        Jazyk ponuky
                      </label>
                    </div>
                  </div>
                  <div>
                    <div class="form-floating mb-3">
                      <Multiselect
                        v-model="currency"
                        :options="currencyOptions"
                        placeholder="Mena"
                        selectLabel=""
                        selectedLabel=""
                        deselectLabel=""
                        deselectGroupLabel=""
                        selectGroupLabel=""
                        :allowEmpty="false"
                      >
                        <span slot="noResult">
                          Oops! No elements found. Consider changing the search
                          query.
                        </span>
                      </Multiselect>
                      <label for="floatingInput" style="top: -13px">
                        Mena
                      </label>
                    </div>
                  </div>
                </div>

                <div class="form-floating mb-3">
                  <Multiselect
                    v-model="nameOfClient"
                    :options="clients"
                    placeholder=""
                    selectLabel=""
                    selectedLabel=""
                    deselectLabel=""
                    deselectGroupLabel=""
                    selectGroupLabel=""
                    :allowEmpty="false"
                  >
                    <span slot="noResult">
                      Oops! No elements found. Consider changing the search
                      query.
                    </span>
                  </Multiselect>
                  <label for="floatingInput" style="top: -13px">
                    Názov klienta
                  </label>
                </div>

                <div class="client-boxes">
                  <div
                    v-for="client in clients"
                    :key="client"
                    class="client-box"
                    @click="nameOfClient = client"
                  >
                    {{ client }}
                  </div>
                </div>

                <div class="input-box">
                  <label>
                    Platnosť ponuky
                    <input v-model="validityDate" type="date" />
                  </label>
                  <div class="i-icon no-circle">
                    <svg
                      width="14"
                      height="13"
                      viewBox="0 0 14 13"
                      fill="none"
                      xmlns="http://www.w3.org/2000/svg"
                    >
                      <path
                        d="M4.88889 7.27783H3.77778C3.59444 7.27783 3.44444 7.14658 3.44444 6.98617V6.01394C3.44444 5.85353 3.59444 5.72228 3.77778 5.72228H4.88889C5.07222 5.72228 5.22222 5.85353 5.22222 6.01394V6.98617C5.22222 7.14658 5.07222 7.27783 4.88889 7.27783ZM7.88889 6.98617V6.01394C7.88889 5.85353 7.73889 5.72228 7.55556 5.72228H6.44444C6.26111 5.72228 6.11111 5.85353 6.11111 6.01394V6.98617C6.11111 7.14658 6.26111 7.27783 6.44444 7.27783H7.55556C7.73889 7.27783 7.88889 7.14658 7.88889 6.98617ZM10.5556 6.98617V6.01394C10.5556 5.85353 10.4056 5.72228 10.2222 5.72228H9.11111C8.92778 5.72228 8.77778 5.85353 8.77778 6.01394V6.98617C8.77778 7.14658 8.92778 7.27783 9.11111 7.27783H10.2222C10.4056 7.27783 10.5556 7.14658 10.5556 6.98617ZM7.88889 9.3195V8.34728C7.88889 8.18686 7.73889 8.05561 7.55556 8.05561H6.44444C6.26111 8.05561 6.11111 8.18686 6.11111 8.34728V9.3195C6.11111 9.47992 6.26111 9.61117 6.44444 9.61117H7.55556C7.73889 9.61117 7.88889 9.47992 7.88889 9.3195ZM5.22222 9.3195V8.34728C5.22222 8.18686 5.07222 8.05561 4.88889 8.05561H3.77778C3.59444 8.05561 3.44444 8.18686 3.44444 8.34728V9.3195C3.44444 9.47992 3.59444 9.61117 3.77778 9.61117H4.88889C5.07222 9.61117 5.22222 9.47992 5.22222 9.3195ZM10.5556 9.3195V8.34728C10.5556 8.18686 10.4056 8.05561 10.2222 8.05561H9.11111C8.92778 8.05561 8.77778 8.18686 8.77778 8.34728V9.3195C8.77778 9.47992 8.92778 9.61117 9.11111 9.61117H10.2222C10.4056 9.61117 10.5556 9.47992 10.5556 9.3195ZM13.2222 3.00005V11.5556C13.2222 12.1997 12.625 12.7223 11.8889 12.7223H2.11111C1.375 12.7223 0.777778 12.1997 0.777778 11.5556V3.00005C0.777778 2.35596 1.375 1.83339 2.11111 1.83339H3.44444V0.569499C3.44444 0.409082 3.59444 0.277832 3.77778 0.277832H4.88889C5.07222 0.277832 5.22222 0.409082 5.22222 0.569499V1.83339H8.77778V0.569499C8.77778 0.409082 8.92778 0.277832 9.11111 0.277832H10.2222C10.4056 0.277832 10.5556 0.409082 10.5556 0.569499V1.83339H11.8889C12.625 1.83339 13.2222 2.35596 13.2222 3.00005ZM11.8889 11.4098V4.16672H2.11111V11.4098C2.11111 11.49 2.18611 11.5556 2.27778 11.5556H11.7222C11.8139 11.5556 11.8889 11.49 11.8889 11.4098Z"
                        fill="#9598A7"
                      />
                    </svg>
                  </div>
                </div>

                <div class="toggles">
                  <Toggle
                    title="Možnosť prijať alebo odmietnuť cenovú ponuku"
                    :value="approveDeclinePriceQuote"
                    :update="(value) => (approveDeclinePriceQuote = value)"
                    :stacked="true"
                  />

                  <Toggle
                    title="Recenzie od mojich klientov"
                    :value="showReviews"
                    :update="(value) => (showReviews = value)"
                    :stacked="true"
                  />

                  <Toggle
                    title="Pridať dobu dodania"
                    :value="addDeliveryDate"
                    :update="(value) => (addDeliveryDate = value)"
                    :stacked="true"
                  />
                </div>
              </div>

              <div class="quote-side right">
                <h6 class="quote-title">Vzhľad a farebná kombinácia</h6>
                <h6 class="quote-subtitle">
                  Vzhľad cenovej ponuky môžeš zmeniť aj neskôr.
                </h6>

                <div class="colors">
                  <div
                    v-for="myColor in defaultColors"
                    :key="myColor"
                    @click="color = myColor"
                    :class="`color-box ${myColor === color ? 'selected' : ''}`"
                  >
                    <div class="inner" :style="`background: ${myColor}`"></div>
                  </div>
                </div>

                <div
                  :class="`custom-color-button ${
                    defaultColors.filter((col) => col === color).length === 0
                      ? 'selected'
                      : ''
                  }`"
                  @click="clickCustomColor"
                >
                  Vlastná farba
                  <input class="color-input" type="color" v-model="color" />
                </div>

                <div
                  class="input-box"
                  style="margin-top: 20px"
                  v-if="
                    defaultColors.filter((col) => col === color).length === 0
                  "
                >
                  <label>
                    HEX kód farby
                    <input v-model="color" />
                  </label>
                </div>

                <div class="hide-logo-toggle-container">
                  <Toggle
                    title="Skryť logo DOFFERO"
                    :value="hideDofferoLogo"
                    :update="(value) => (hideDofferoLogo = value)"
                  />
                  <img
                    :src="i"
                    class="i-icon"
                    style="margin-left: 10px; margin-top: -2px"
                    data-bs-toggle="tooltip"
                    data-bs-placement="top"
                    title="tooltip on top"
                  />
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>

    <div class="my-container common-border-shadow template-use">
      <div class="select-template-buttons">
        <div style="text-align: left">
          <p class="template-use-title">
            Nahraj si položky z dopredu vytvorenej šablóny
          </p>
          <p class="template-use-desc">
            Uľahči si prácu a vytvor si šablóny ponúk, ktoré vytváraš
            najčastejšie.
          </p>
        </div>
        <div
          class="green-button"
          @click="showTemplateSelect = !showTemplateSelect"
        >
          Vybrať šablónu
        </div>
      </div>
      <div
        class="form-floating type-item template-use-select"
        v-if="showTemplateSelect"
      >
        <Multiselect
          v-model="templateSelect"
          :options="selectOptions"
          placeholder=""
          selectLabel=""
          selectedLabel=""
          deselectLabel=""
          deselectGroupLabel=""
          selectGroupLabel=""
          :allowEmpty="false"
        >
          <span slot="noResult">
            Oops! No elements found. Consider changing the search query.
          </span>
        </Multiselect>
        <label for="floatingInput" style="top: -13px"> Šablóna </label>
      </div>
    </div>

    <div class="my-container common-border-shadow">
      <div class="top-inputs">
        <div class="logo-input-container common-border-shadow">
          <img :src="logo" />
          <label class="logo-upload-text">
            <span> Zmeniť logo </span>
            <input
              class="logo-input"
              type="file"
              placeholder="Change logo"
              @change="logoInput"
            />
          </label>
        </div>
        <div class="small-inputs-container common-border-shadow">
          <input placeholder="Názov firmy" v-model="companyName" />
          <input placeholder="Adresa / Ulica" v-model="companyStreet" />
          <input placeholder="Mesto / PSČ" v-model="companyTownZipcode" />
        </div>
        <div class="small-inputs-container common-border-shadow">
          <input placeholder="IČO" v-model="ico" />
          <input placeholder="DIČ" v-model="dic" />
          <input placeholder="IČ DPH" v-model="icDph" />
        </div>
      </div>

      <input
        class="big-input-title common-border-shadow"
        placeholder="Názov cenovej ponuky"
        v-model="title"
      />
      <input
        class="input-welcome-text common-border-shadow"
        placeholder="Popis cenovej ponuky"
        v-model="welcomeText"
      />

      <div class="toggles">
        <Toggle
          title="Zobraziť sumárne čísla"
          :value="showSummaryNumbers"
          :update="(value) => (showSummaryNumbers = value)"
        />

        <Toggle
          v-if="showSummaryNumbers"
          title="Pridať automatický súčet nakoniec"
          :value="addTotalEnd"
          :update="(value) => (addTotalEnd = value)"
        />
      </div>

      <div class="summaries-container" v-show="showSummaryNumbers">
        <div class="summaries">
          <div class="summary new" v-if="summaries.length < 3">
            <input
              v-model="newSummaryTitle"
              class="input-new-summary-title"
              placeholder="Nadpis"
              @keydown.prevent.enter="addNewSummary"
            />
            <div class="d-flex justify-content-center align-items-center">
              <input
                v-model="newSummaryValue"
                class="input-new-summary-value"
                placeholder="Suma"
                @keydown.prevent.enter="addNewSummary"
              />
              <!-- <span>&euro;</span> -->
            </div>
          </div>

          <div
            class="summary"
            v-for="(summary, index) in summaries"
            :key="index"
          >
            <div class="delete-btn" @click="deleteSummary(index)">
              <svg
                width="12"
                height="16"
                viewBox="0 0 12 16"
                fill="none"
                xmlns="http://www.w3.org/2000/svg"
              >
                <path
                  d="M3.33301 3.99998V2.66665C3.33301 2.31302 3.47348 1.97389 3.72353 1.72384C3.97358 1.47379 4.31272 1.33331 4.66634 1.33331H7.33301C7.68663 1.33331 8.02577 1.47379 8.27582 1.72384C8.52587 1.97389 8.66634 2.31302 8.66634 2.66665V3.99998M10.6663 3.99998V13.3333C10.6663 13.6869 10.5259 14.0261 10.2758 14.2761C10.0258 14.5262 9.68663 14.6666 9.33301 14.6666H2.66634C2.31272 14.6666 1.97358 14.5262 1.72353 14.2761C1.47348 14.0261 1.33301 13.6869 1.33301 13.3333V3.99998H10.6663Z"
                  stroke="#9598A7"
                  stroke-linecap="round"
                  stroke-linejoin="round"
                />
              </svg>
            </div>
            <Editable
              v-model="summary.title"
              :class="`summary-title editable ${summary.title ? '' : 'empty'}`"
              placeholder="Nadpis"
            />
            <div class="d-flex">
              <Editable
                v-model="summary.value"
                :class="`summary-amount editable ${
                  summary.value ? '' : 'empty'
                }`"
                placeholder="Suma"
              />

              <span v-show="summary.value">&euro;</span>
            </div>
            <!-- <p class="summary-title">{{ summary.title }}</p>
            <p class="summary-amount">{{ summary.value }} &euro;</p> -->
          </div>
          <div class="summary total" v-if="addTotalEnd">
            <p class="summary-title">Total</p>
            <p class="summary-amount">{{ summariesTotal }}&euro;</p>
          </div>
        </div>
        <div
          :class="`common-border-shadow add-new-summary-btn ${
            summaries.length < 3 ? '' : 'disabled'
          }`"
          @click="addNewSummary"
        >
          + Pridať sumárne číslo
          <span v-if="summaries.length < 3"> {{ summaries.length + 1 }}/3</span>
        </div>
      </div>
      <div class="items-container">
        <Draggable v-model="items" :handle="'.drag-area'">
          <transition-group>
            <Item
              v-for="(item, index) in items"
              :key="`${item.timestamp}`"
              :item="item"
              :index="index"
              :deleteItem="deleteItem"
              :moveUp="moveUp"
              :moveDown="moveDown"
              :totalLength="items.length"
              :addSubItem="addSubItem"
            />
          </transition-group>
        </Draggable>

        <div
          @click="addNewItem"
          class="blue-button large"
          style="margin-top: 15px"
        >
          + Pridať novu položku
        </div>
      </div>

      <div style="width: 100%">
        <div class="blue-container totals">
          <div class="totals-toggles">
            <div class="total-toggles-container">
              <Toggle
                :value="totalDiscount"
                :update="(value) => (totalDiscount = value)"
                title="Pridať celkovú zľavu"
              />
              <input
                class="simple-bottom-border-input"
                v-model="totalDiscountValue"
              />
              %
            </div>

            <div class="total-toggles-container">
              <Toggle
                :value="vatEnable"
                :update="(value) => (vatEnable = value)"
                title="Nastaviť cenu s"
              />
              <input
                class="simple-bottom-border-input"
                v-model="vatValue"
              /><span>% DPH</span>
            </div>
          </div>

          <div class="total-without-vat top-price" v-if="totalDiscount">
            <span>Cena spolu:</span>
            <del>
              <div class="red-badge">Zľava {{ totalDiscountValue }}%</div>
              {{ total.from }} &euro;
              <span v-if="total.to !== total.from">
                - {{ total.to }} &euro;
              </span>
            </del>
          </div>

          <div class="total-with-vat" v-if="vatEnable">
            <span>Cena s DPH ({{ vatValue }}%)</span>
            <span class="price">
              {{ totalWith21VAT.from }} &euro;
              <span v-if="totalWith21VAT.to !== totalWith21VAT.from">
                - {{ totalWith21VAT.to }} &euro;
              </span>
            </span>
          </div>
          <div class="total-without-vat">
            <span>
              Cena
              <span v-if="vatEnable">bez DPH</span>
              <span v-if="totalDiscount"> po zľave </span>
            </span>
            <span class="price">
              <span v-if="total.sum || total.sum === 0">
                <span
                  v-if="totalDiscount && parseFloat(totalDiscountValue) > 0"
                >
                  {{ totalWithDiscount.sum }} &euro;
                </span>
                <span v-else>{{ total.sum }} &euro;</span>
              </span>
              <span v-else>
                <span
                  v-if="totalDiscount && parseFloat(totalDiscountValue) > 0"
                >
                  {{ totalWithDiscount.from }} &euro; -
                  {{ totalWithDiscount.to }} &euro;
                </span>
                <span v-else>
                  {{ total.from }} &euro; - {{ total.to }} &euro;
                </span>
              </span>
            </span>
          </div>
        </div>
      </div>
    </div>

    <div class="my-container common-border-shadow offer">
      <div class="offer-footer">
        <div class="left">
          <div class="box common-border-shadow">
            <p class="info">{{ companyName }}</p>
            <p class="info">{{ companyStreet }}</p>
            <p class="info">{{ companyTownZipcode }}</p>
            <p class="info">IČO: {{ ico }}</p>
            <p class="info">DIČ: {{ dic }}</p>
            <p class="info">IČ DPH:{{ icDph }}</p>
          </div>
        </div>
        <div class="middle">
          <div class="middle-text">platnosť cenovej ponuky do 21. 10. 2020</div>
          <img src="/offer-footer.png" />
        </div>
        <div class="right">
          <div class="box common-border-shadow stacked">
            <p class="info">info@matate.sk</p>
            <p class="info">+421 944 439 749</p>
            <p class="info">www.matate.sk</p>
          </div>
          <div class="box common-border-shadow stacked"></div>
        </div>
      </div>
    </div>

    <div class="footer-buttons">
      <div class="left">
        <div class="simple-button">Späť</div>
        <div class="simple-text">
          Automaticky uložené <span class="gray-text">11:40:42</span>
        </div>
      </div>
      <div class="right">
        <div class="green-button">Uložiť</div>
        <div class="green-button">Uložiť ako šablónu</div>
        <button
          type="submit"
          value="shit"
          class="green-button filled"
          @click="finish"
        >
          Publikovať ponuku
        </button>
      </div>
    </div>
    <div class="final-buttons">
      <div class="gray-button">
        <svg
          width="14"
          height="14"
          viewBox="0 0 14 14"
          fill="none"
          xmlns="http://www.w3.org/2000/svg"
        >
          <path
