<template>
  <div :class="`form-check form-switch ${stacked ? 'stacked' : ''}`">
    <input
      class="form-check-input"
      type="checkbox"
      :id="`${timestamp}-${title}`"
      :value="value"
      @change="changeValue"
      :checked="value"
    />
    <label class="form-check-label" :for="`${timestamp}-${title}`">{{
      title
    }}</label>
  </div>
</template>

<script>
export default {
  name: "Toggle",
  props: {
    title: {
      required: true,
    },
    value: {
      required: true,
    },
    update: {
      required: true,
    },
    stacked: {
      required: false,
      default: false,
    },
  },
  data() {
    return {
      timestamp: Date.now(),
    };
  },
  methods: {
    changeValue(e) {
      this.update(e.target.checked);
    },
  },
};
</script>

<style lang="scss">
.form-switch .form-check-input {
  height: 18px;
  width: 34px;
  margin-right: 5px;
}
.form-switch .form-check-input:focus {
  border-color: rgba(0, 0, 0, 0.25);
  outline: 0;
  box-shadow: 0 0 0 0 rgba(0, 0, 0, 0);
  background-image: url("data:image/svg+xml,<svg xmlns='http://www.w3.org/2000/svg' viewBox='-4 -4 8 8'%3e%3ccircle r='3' fill='rgba(0,0,0,0.25)'/></svg>");
}
.form-switch .form-check-input:checked {
  background-color: #2ed8b6;
  border-color: #2ed8b6;
  border: none;
  background-image: url("data:image/svg+xml,<svg xmlns='http://www.w3.org/2000/svg' viewBox='-4 -4 8 8'%3e%3ccircle r='3' fill='rgba(255,255,255,1.0)'/></svg>");
}

.form-check.form-switch {
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;

  input,
  label {
    cursor: pointer;
  }

  + .form-check.form-switch {
    margin-left: 20px;
  }
}

.stacked {
  &.form-check.form-switch {
    + .form-check.form-switch {
      margin-left: 0;
    }
  }
}
</style>
