<template>
  <p
    ref="editable"
    contenteditable
    v-on="listeners"
    :placeholder="placeholder"
  />
</template>

<script>
export default {
  props: {
    value: {
      type: String,
      default: "",
    },
    placeholder: {
      type: String,
      required: false,
      default: "Type..",
    },
  },
  computed: {
    listeners() {
      return { ...this.$listeners, input: this.onInput };
    },
  },
  mounted() {
    this.$refs.editable.innerText = this.value;
  },
  methods: {
    onInput(e) {
      this.$emit("input", e.target.innerText);
    },
  },
};
</script>

<style lang="scss" scoped>
p[placeholder]:empty:before {
  content: attr(placeholder);
  color: #888;
}

p[placeholder]:focus:before {
  content: none !important;
}
</style>
